
.page-header h1 {
  font-weight: 300;
}

.menubar {
  margin-bottom: 16px;
}

.card {
  margin-bottom: 10px;
}
.card:last-child {
  margin-bottom: 25px;
}
.card-block {
  padding: 15px;
}
.add-item {
  margin: 30px 0;
}

.add-item input {
  outline: none;
  border: 0;
  border-bottom: 1px solid rgba(255, 255, 255, 0.5);
  width: 100%;
  -webkit-transition: all 0.25s;
  transition: all 0.25s;
  background: inherit;
  color: white;
}
.add-item input:focus {
  border-bottom-color: rgba(255, 255, 255, 1);
}

.card-body > * {
  min-height: 50px;
}

.card.task-lane-item {
  background: #627180;
}
